*{
    padding: 0;
    margin: 0;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.app{
/*     flex-wrap: wrap; */
    background-color: #00be98;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0;
    margin: 0;
}
.container-title{
    height: 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
h1{
    text-align: center;
    display: block;
    font-size: 50px;
    color: white;
    text-shadow: 1px 1px 10px black;
    margin-bottom: .1em;
}
button{
    border: none;
    padding: .5em;
    margin: 0;
    margin-bottom: .5em;
    border-radius: 1em;
    cursor: pointer;
    background-color: #f9cb35;
    font-weight: bold;
    color: white;
    font-size: 15px;
    outline: none;
    &:hover{
        box-shadow: inset 1px 4px 4px #9b7221;
        color: rgb(46, 45, 41);
    }
}
.cards-container{
/*     display: flex;
    flex-wrap: wrap; */
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(4, 1fr);
    width: 60vw;

    border-radius: 1em;
    background-color: #fff;
    padding: 2em;

}
.card{
    display: block;
    margin: 0 auto;
    height: calc(100% - 10px);
    width: calc(100% - 10px);
    cursor: pointer;

    &:hover {
        transform: scale(0.98);
    }
}
.card-image{
/*     height: calc(150px - 10px);
    width: calc(142.8px - 10px); */
    height: 100%;
    width: 100%;
    border-radius: 50%;
}

@media screen and (max-width: 700px) {

    .cards-container {
        grid-template-columns: repeat(4, 1fr);

    }
    .card{
        height: calc(90% - 10px);
        width: calc(90% - 10px);
  }
}